.main
  flex: 1 1 auto

  &.is-main
    padding-top: 220px
    padding-bottom: 190px
    background: url('../img/background-stars.png') no-repeat top center
    background-size: cover
    background-color: $bgBlue

  &.is-info
    padding-top: 22px
    padding-bottom: 118px

  &.is-download
    padding-top: 60px
    padding-bottom: 190px
    a
      color: $textBlue

  &.is-error
    padding-top: 100px
    padding-bottom: 150px
    background: url('../img/bg-404.png') no-repeat
    background-size: cover